/**Compiled Sass Files**/
.board-root {
  --hex-size: calc(100vh / 4.375);
  --hex-height: calc(var(--hex-size));
  --hex-width: calc(var(--hex-size) * 8 / 9);
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  user-select: none;
  -webkit-user-drag: none;
  background-color: #00b0ff;
}
.container-root {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  height: 100%;
  user-select: none;
  -webkit-user-drag: none;
  justify-content: center;
}
.container {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  height: auto;
  position: relative;
  user-select: none;
  -webkit-user-drag: none;
}
.line {
  display: flex;
  justify-content: center;
  margin-top: calc(var(--hex-height) * 0.25 * -1);
  gap: 0;
}
.line:nth-child(1) {
  margin-top: 0;
}
.hex-wrapper {
  display: inline-block;
  position: relative;
  background-color: #d3bc83;
  height: var(--hex-height);
  width: var(--hex-width);
  clip-path: polygon(0% 25%, 0% 75%, 50% 100%, 100% 75%, 100% 25%, 50% 0%);
}
.hex {
  position: absolute;
  top: 2px;
  bottom: 2px;
  left: 2px;
  right: 2px;
  clip-path: polygon(0% 25%, 0% 75%, 50% 100%, 100% 75%, 100% 25%, 50% 0%);
  display: flex;
  justify-content: center;
  align-items: center;
}
.edge {
  position: absolute;
  width: 20px;
  height: calc(var(--hex-height) / 2);
  background: transparent;
  z-index: 10;
  border-radius: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.edge .input-circle {
  height: 1rem;
  width: 1rem;
  border-radius: 100%;
  animation: glow 1s ease-in-out infinite alternate;
  transition: transform 400ms;
  cursor: pointer;
}
.edge .input-circle:hover {
  transform: scale(2);
  animation: none;
  box-shadow: #0056796a 0 0 1px 2px;
}
.edge .input-circle:active {
  transform: scale(1.1);
  animation: none;
  box-shadow: #0056796a 0 0 4px 4px;
}
@keyframes glow {
  from {
    box-shadow: #0b861baa 0 0 1px 2px;
  }
  to {
    box-shadow: rgba(0, 82, 27, 0.667) 0 0 6px 4px;
  }
}
.road {
  width: 10px;
  height: calc(var(--hex-height) / 3);
  z-index: 10;
  border-radius: 1rem;
  position: absolute;
  cursor: pointer;
}
.road.BLACK {
  background-color: #000000;
}
.road.WHITE {
  background-color: #ffffff;
}
.road.YELLOW {
  background-color: #ffd900;
}
.road.ORANGE {
  background-color: #ff5e00;
}
.road.GREEN {
  background-color: #007e3f;
}
.road.BLUE {
  background-color: #0000f2;
}
.road.PURPLE {
  background-color: purple;
}
.vertex {
  position: absolute;
  background: transparent;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1rem;
  width: 1rem;
}
.vertex .input-circle {
  height: 1rem;
  width: 1rem;
  border-radius: 100%;
  animation: glow 1s ease-in-out infinite alternate;
  cursor: pointer;
  transition: transform 400ms;
  background-color: #cfac62;
}
.vertex .input-circle:hover {
  transform: scale(2);
  animation: none;
  box-shadow: #0056796a 0 0 1px 2px;
}
.vertex .input-circle:active {
  transform: scale(1.1);
  animation: none;
  box-shadow: #0056796a 0 0 4px 4px;
}
.vertex .settlement {
  cursor: pointer;
}
.vertex .settlement svg {
  height: 24px;
  pointer-events: all;
}
.vertex .settlement path {
  pointer-events: all;
}
.vertex .settlement.BLACK path {
  fill: #000000;
}
.vertex .settlement.WHITE path {
  fill: #ffffff;
}
.vertex .settlement.YELLOW path {
  fill: #ffd900;
}
.vertex .settlement.ORANGE path {
  fill: #ff5e00;
}
.vertex .settlement.GREEN path {
  fill: #007e3f;
}
.vertex .settlement.BLUE path {
  fill: #0000f2;
}
.vertex .settlement.PURPLE path {
  fill: purple;
}
.port {
  position: absolute;
  background: transparent;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(var(--hex-height) / 2);
}
.port rect {
  fill: #d8b244;
}
